.n-wrapper{
    height: 10vh;
    display: flex;
    justify-content: space-between;
    
}

.n-left{
    flex: 1;
    align-items: center;
    display: flex;
    gap: 2rem;
}
.n-name{
    font-size: 1.3rem;
    font-weight: bold;
}


.n-right{
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    font-weight: 400;
}
.n-list{
    flex: 10;

}
.n-list > ul{
    display: flex;
    gap: 2rem;
    margin-right: 4rem;
    list-style: none;
}
.nav_li:hover{
    cursor: pointer;
    color: var(--orange);
}
.n-button{
    flex: 2;
}
.n-button:hover{
    cursor: pointer;
    background: white;
    color: var(--orange);
    border: 1px solid orange;
  }


  @media screen and (max-width:480px) {
   .n-list{
    display: none;
   }   
   .n-button{
    display: none;
   }
}