.portfolio{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 70vh;
    margin-top: 6rem;
}
.portfolio span:nth-child(1){
    color: black;
    font-size: 2rem;
    font-weight: bold;
}
.portfolio span:nth-child(2){
    color: orange;
    font-size: 2.5rem;
    font-weight: bold;
}

/* slider css */
.portfolio .swiper{
    overflow: visible!important;
}
.portfolio-slider{
    margin-top: 3rem;
    width: 100%;
}
.portfolio-slider .swiper-slide{
    width: 20rem;

}
.portfolio img{
    width: 23rem;
    height: 32vh;
    filter: drop-shadow(-12px 15px 13px rgba(0,0,0,0.25));
    border-radius: 19px;
}


@media screen and (max-width:480px){
 .portfolio img{
    width: 16rem;
 }
}